<template>
    <b-container fluid class="content">
        <b-row>
            <b-col>
            <b-card border-variant="light" header="Test Send Notification" class="text-center">
                <alert-fail ref="alertFail"> {{ messageError }} </alert-fail>
                <div class="form-group row text-left mx-lg-5">
                    <label class="col-sm-2 col-form-label">Token :</label>
                    <div class="col-sm-10">
                    <b-form-input v-model="deviceToken" size="sm" placeholder="Required : json array token"></b-form-input>
                    </div>
                    <label class="col-sm-2 col-form-label">App ID:</label>
                    <div class="col-sm-10">
                    <b-form-input v-model="appId" size="sm" placeholder="Required"></b-form-input>
                    </div>
                    <label class="col-sm-2 col-form-label">Username:</label>
                    <div class="col-sm-10">
                    <b-form-input v-model="username" size="sm" placeholder="Required"></b-form-input>
                    </div>
                    <label class="col-sm-2 col-form-label">API Key:</label>
                    <div class="col-sm-10">
                    <b-form-input v-model="apiKey" size="sm" placeholder="Required"></b-form-input>
                    </div>
                    <label class="col-sm-2 col-form-label">Platform:</label>
                    <div class="col-sm-10 text-left">
                    <b-form-radio-group
                        :options="options"
                        buttons  button-variant="outline-secondary"
                        size="sm"
                        v-model="platformSelected"
                    ></b-form-radio-group>
                    </div>
                    <label class="col-sm-2 col-form-label">Action:</label>
                    <div class="col-sm-10 text-left">
                        <b-button size="sm" @click="submit" variant="outline-primary">send</b-button>
                        <b-button class="ml-1" size="sm" variant="outline-secondary">Load previous send</b-button>
                    </div>

                </div>
                <hr/>
                <b-card-text>
                    <b-card bg-variant="light" header="Request" class="text-left">
                        <b-card-text><pre> {{ requestJSON }} </pre></b-card-text>
                    </b-card>
                    <hr/>
                    <b-card bg-variant="light" header="Response" class="text-left" >
                        <b-card-text><pre> {{ responseJSON }} </pre></b-card-text>
                    </b-card>
                </b-card-text>
            </b-card>
            </b-col>
        </b-row>
    </b-container>
</template>

<script>
import AlertFail from '@/components/AlertFail.vue';

export default {
  data () {
    return {
      deviceToken: '',
      appId: '',
      username: '',
      apiKey: '',
      requestJSON: {},
      responseJSON: { error: [], message: 112 },
      platformSelected: 'ios',
      options: [
        { text: 'ios', value: 'ios' },
        { text: 'android', value: 'android', disabled: true }
      ],
      messageError: ''
    };
  },
  methods: {
    submit () {
      this.$refs.alertFail.hide();
      try {
        const json = {
          appId: this.appId,
          username: this.username,
          apiKey: this.apiKey,
          deviceToken: this.stringToJSON(this.deviceToken),
          notification: {
            payload: { test: 'version 1' },
            title: 'Hello ทดสอบ',
            badge: 1,
            message: 'This is for test, นี่คือการทดสอบ'
          }
        };
        this.requestJSON = json;
        const url = process.env.VUE_APP_SERVICE_HOST + '/notification/' + this.platformSelected + '/push';
        this.axios.post(url, json)
          .then(response => {
            this.responseJSON = response.data;
          })
          .catch(error => {
            if (error.response) {
              this.responseJSON = error.response.data;
            } else {
              throw error;
            }
          });
      } catch (error) {
        this.messageError = error.message;
        this.$refs.alertFail.show();
      }
    },
    stringToJSON (string) {
      try {
        return JSON.parse(string);
      } catch (error) {
        // eslint-disable-next-line no-throw-literal
        error.message = 'Error to parse value : ' + string + ', ' + error.message;
        throw error;
      }
    }
  },
  components: {
    AlertFail
  }
};
</script>

<style scoped>
    .content{
        font-size: 0.8em;
    }
    .form-test{
        margin-left: 20px;
        /* margin-right: 20px; */
    }
</style>
